
import {defineComponent, reactive, toRefs, SetupContext} from 'vue'

interface Data {
  qrcodeUrl: string,
  value: string
}

export default defineComponent({
  name: "",
  components: {},
  props: {},
  setup(props, ctx: SetupContext) {
    let data: Data = reactive<Data>({
      qrcodeUrl: '',
      value: ''
    })
    let handleClick = () => {
      data.qrcodeUrl = data.value
      console.log(data.qrcodeUrl)
    }
    return {
      ...toRefs(data),
      handleClick,
    }
  }
})
